body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.field{width:643px;height:643px;border:1px solid black;background:#08ce6b;margin:auto}.field.hard{width:620px;height:620px}.row_new{display:flex;flex-wrap:wrap;margin-right:-15px}

.cell{color:#000000;font-size:30px;box-sizing:border-box;border:3px solid #44b657;background:#618066;border-radius:10px;transition:all 0.3s;display:flex;justify-content:center;align-items:center;cursor:pointer;background-repeat:no-repeat;background-size:contain;background-position:center}.cell.house{background-image:url(/react-game/static/media/house.ceef0927.png)}.cell.red-hat{background-image:url(/react-game/static/media/redHat.79395290.png)}.cell.wolf{background-image:url(/react-game/static/media/wolf.19241363.png)}.cell.tree{background-image:url(/react-game/static/media/tree.e4829403.png)}.cell.grandma{background-image:url(/react-game/static/media/grandma.e4f8156f.png)}.easy .cell{width:160px;height:160px}.hard .cell{width:77px;height:77px}

.game{width:1024px;height:768px;margin:auto}.game h1{text-align:center;font-size:50px;font-style:oblique;color:white}.game.day{background-color:#1c718e;background:linear-gradient(to top, #fefce9, #1c718e);padding:10px;border:1px solid #353535}.game.night{background-color:#062029;background:linear-gradient(to top, #42402b, #062029);padding:10px;border:1px solid #353535}.count{text-align:center;font-size:25px;color:white}.custom-control-label{color:white}.game_opt{position:absolute}.table{color:white;background-color:#3c4c52;font-size:13px;width:14px}.table th{padding:0.5rem}.game_ul{color:white;list-style:none;padding:0;padding:initial}.rss_logo{width:108px;position:absolute;top:10px}

.music_img{width:30px;height:30px}

.win{width:600px;height:600px;border:1px solid black;margin:auto;background:#08ce6b;background-repeat:no-repeat}.win_img{width:400px;height:400px;margin-left:80px;margin-top:40px}.win_btn{position:absolute;margin-left:-250px}

